    grid-column: 1 / 11;
    grid-row: 1 / 3;

  }
  
  .search-styles.dynamic-area {
    grid-column: 1 / 11;
    grid-row: 3 / 10;

    /* border: 3px solid hotpink; */
  }

  .search-styles.dynamic-area form {
    width: 80%;
  }

}
